@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
.main-container{
  min-height:calc(100% - 64px - 84px);
  padding-bottom:60px !important
}

.main-container-none-header{
  min-height:calc(100% - 64px);
  padding-bottom:60px !important
}

footer{
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:center;
  justify-content:center;
  -webkit-align-items:center;
  align-items:center;
  width:100%;
  height:40px;
  margin-top:-40px !important;
  color:#EEEEEE;
  background-color:#2487a6
}


.tooltip-box{
  position:relative;
  background-position:0px 0px
}

.tooltip-box:hover{
  background-position:0px 0px
}

.tooltip-box:hover>.tooltip,.tooltip-box:active>.tooltip{
  visibility:visible
}

.tooltip{
  visibility:hidden;
  width:200px;
  background-color:rgba(0,0,0,0.7);
  color:#fff;
  text-align:center;
  border-radius:6px;
  position:absolute;
  z-index:1;
  bottom:120%;
  left:0;
  margin-left:-80px;
  z-index:200
}

.tooltip::after{
  content:"";
  position:absolute;
  top:100%;
  left:50%;
  margin-left:-5px;
  border-width:5px;
  border-style:solid;
  border-color:black transparent transparent transparent
}

@media (max-width: 767px){
  .tooltip{
    width:150px;
    bottom:130%;
    left:0;
    margin-left:-110px
  }

  .tooltip::after{
    top:100%;
    left:80%;
    margin-left:0px
  }
}

